body {
 padding: 0;
 font-family: 'Times New Roman', Times, serif;
  margin: 0;
}
.main{
  
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: #8566aa;
  text-align: center;
}

.center{
  position: absolute;
  width: 25%;
  height: 80vh;
  background: white;
  box-shadow: 5px 5px 5px -5px rgba(0, 0, 0, .5);
  border-radius: 15px;
  overflow: scroll;
}
h1{
  color: white;
  background: transparent;
  background-color: #8566aa;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  text-shadow: 5px 5px 15px -5px rgba(0, 0, 0, .3);
}
input{
  height: 30px;
  width: 60%;
  background: transparent;
  outline: none;
  border: none;
  border-bottom: 2px solid #8566aa;
  font-size: 20px;
  font-weight: 500;
  top: 10px;
  
}
button{
  min-height: 40px;
  width: 40px;
  margin-left: 10px;
  border-radius: 50%;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, .3);
  background: transparent;
  background-color: #8566aa;
  border: none;
  outline: none;
  font-size: 40px;
  font-weight: 500;

  
  color: white;

}
button:hover{
  background-color: green;
}
ol{
  margin-top: 30px;
}
ol li{
  padding-left: 0;
  font-size: 20px;
  font-weight: 500;
  text-align: left;
  align-items: center;
  display: flex;
  min-height: 40px;
  color: #8566aa;
  text-transform: capitalize;
}
.btn{
  width: 50px;
  height: 55px;
  background-color: purple;
  color: white;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, .3);
  border-radius: 50%;
  display: flex;
  

}
.todo .del:hover{
  color: purple;
  background-color: white;
  transition: .3s;
}
.todo .del{
  width: 25px;
  height: 25px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin: 0 15px 6px 3px;
  cursor: pointer;
  padding: 5px;
  background-color: purple;
  color: white;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, .3);
}
/* span{
  display: inline-block;
} */
.in{
  display: inline-block;
}
@media (max-width:768px){
  .center{
    width: 96vw;
  }
}
@media (max-width:1070px) and (min-width:767px){
  .center{
    width: 70vw;
  }
}